h1 {
  color: black;
  text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
  text-align: center;

  font-family: Verdana;
}
h2 {
  color: #e7e08b;
  text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.1);
  text-align: center;
  font-family: Verdana;
}
h3 {
  color: black;
  text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
  text-align: center;
  font-family: Verdana;
}
info {
  color: black;
  text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
  text-align: center;

  font-family: Verdana;
}

.note {
  color: #e7e08b;
  text-align: center;
  background: #336699;
  padding: 25px;
  margin: 25px;
  width: 500px;
  list-style-type: none; /* Remove bullets */
  min-height: 50px;
  font-size: 15px;
  border-radius: 5px;
}
.noteImportant {
  color: #e7e08b;
  text-align: center;
  background: #07315a;
  border-color: gold;
  padding: 25px;
  margin: 25px;
  width: 500px;
  list-style-type: none; /* Remove bullets */
  min-height: 50px;
  font-size: 15px;
  border-radius: 5px;
}

li {
  -webkit-column-break-inside: avoid;
  page-break-inside: avoid;
  break-inside: avoid;
}

.error {
  color: red;
  background: lightgrey;
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}
